import React from "react";
import classNames from "classnames";
import { StyledProps } from "../_type";
import { useDefault } from "../_util/use-default";
import { Icon } from "../icon";
import { FadeTransition } from "../transition";
import { useConfig } from "../_util/config-context";

export interface AlertProps extends StyledProps {
  /**
   * 
   * @default "info"
   */
  type?: "info" | "success" | "warning" | "error";

  /**
   * 
   */
  children?: React.ReactNode;

  /**
   *  Alert 
   *  `onClose` 
   */
  visible?: boolean;

  /**
   *  Alert  defaultVisible  true
   * @default true
   */
  defaultVisible?: boolean;

  /**
   *  visibile  defaultVisible  onClose
   */
  onClose?: () => void;
}

export function Alert(props: AlertProps) {
  const { type, children, className, style, onClose } = props;
  const { classPrefix } = useConfig();

  const alertClassName = classNames(
    `${classPrefix}-alert`,
    {
      [`${classPrefix}-alert--${type}`]: type,
    },
    className
  );

  const [visible, onVisibleChange] = useDefault(
    props.visible,
    props.defaultVisible,
    visible => !visible && onClose && onClose()
  );

  const isClosable = typeof visible === "boolean";

  const alertElement = (
    <div className={alertClassName} style={style}>
      <div className={`${classPrefix}-alert__info`}>{children}</div>
      {isClosable && (
        <Icon
          link
          type="close"
          aria-label="close"
          onClick={() => onVisibleChange(false)}
        />
      )}
    </div>
  );

  if (isClosable) {
    return <FadeTransition in={visible}>{alertElement}</FadeTransition>;
  }

  return alertElement;
}
